"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import Link from "next/link";

interface PontoAtendimento {
  id: string;
  nome: string;
  endereco: string;
  cidade: string;
  horarios: string[];
  capacidade_por_horario: number;
  status: "ativo" | "inativo";
}

interface Agendamento {
  id: number;
  nome: string;
  telefone: string;
  email: string;
  cidade: string;
  ponto_atendimento_id: string;
  data_agendamento: string;
  horario: string;
  status: string;
  ponto_nome: string;
  ponto_endereco: string;
}

export default function AdminPage() {
  const [pontos, setPontos] = useState<PontoAtendimento[]>([]);
  const [agendamentos, setAgendamentos] = useState<Agendamento[]>([]);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingPonto, setEditingPonto] = useState<PontoAtendimento | null>(null);
  const [formData, setFormData] = useState({
    nome: "",
    endereco: "",
    cidade: "",
    horarios: "",
    capacidade_por_horario: 1,
    status: "ativo" as "ativo" | "inativo"
  });

  useEffect(() => {
    loadPontos();
    loadAgendamentos();
  }, []);

  const loadPontos = async () => {
    try {
      const response = await fetch("/api/pontos-atendimento");
      if (response.ok) {
        const data = await response.json();
        setPontos(data.pontos);
      }
    } catch (error) {
      console.error("Erro ao carregar pontos:", error);
    }
  };

  const loadAgendamentos = async () => {
    try {
      const response = await fetch("/api/agendamentos");
      if (response.ok) {
        const data = await response.json();
        setAgendamentos(data.agendamentos);
      }
    } catch (error) {
      console.error("Erro ao carregar agendamentos:", error);
    }
  };

  const handleInputChange = (field: string, value: string | number) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    const pontoData = {
      ...formData,
      horarios: formData.horarios.split(",").map(h => h.trim()).filter(h => h)
    };

    try {
      const url = editingPonto 
        ? `/api/pontos-atendimento/${editingPonto.id}`
        : "/api/pontos-atendimento";
      
      const method = editingPonto ? "PUT" : "POST";
      
      const response = await fetch(url, {
        method,
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(pontoData)
      });

      if (response.ok) {
        alert(editingPonto ? "Ponto atualizado com sucesso!" : "Ponto criado com sucesso!");
        setIsDialogOpen(false);
        setEditingPonto(null);
        setFormData({
          nome: "",
          endereco: "",
          cidade: "",
          horarios: "",
          capacidade_por_horario: 1,
          status: "ativo"
        });
        loadPontos();
      } else {
        alert("Erro ao salvar ponto de atendimento");
      }
    } catch (error) {
      console.error("Erro:", error);
      alert("Erro ao salvar ponto de atendimento");
    }
  };

  const handleEdit = (ponto: PontoAtendimento) => {
    setEditingPonto(ponto);
    setFormData({
      nome: ponto.nome,
      endereco: ponto.endereco,
      cidade: ponto.cidade,
      horarios: ponto.horarios.join(", "),
      capacidade_por_horario: ponto.capacidade_por_horario,
      status: ponto.status
    });
    setIsDialogOpen(true);
  };

  const handleDelete = async (id: string) => {
    if (!confirm("Tem certeza que deseja excluir este ponto de atendimento?")) {
      return;
    }

    try {
      const response = await fetch(`/api/pontos-atendimento/${id}`, {
        method: "DELETE"
      });

      if (response.ok) {
        alert("Ponto excluído com sucesso!");
        loadPontos();
      } else {
        alert("Erro ao excluir ponto de atendimento");
      }
    } catch (error) {
      console.error("Erro:", error);
      alert("Erro ao excluir ponto de atendimento");
    }
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "ativo":
        return <Badge className="bg-green-100 text-green-800">Ativo</Badge>;
      case "inativo":
        return <Badge className="bg-red-100 text-red-800">Inativo</Badge>;
      case "agendado":
        return <Badge className="bg-blue-100 text-blue-800">Agendado</Badge>;
      case "cancelado":
        return <Badge className="bg-gray-100 text-gray-800">Cancelado</Badge>;
      case "concluido":
        return <Badge className="bg-green-100 text-green-800">Concluído</Badge>;
      default:
        return <Badge>{status}</Badge>;
    }
  };

  const stats = {
    totalPontos: pontos.length,
    pontosAtivos: pontos.filter(p => p.status === "ativo").length,
    totalAgendamentos: agendamentos.length,
    agendamentosHoje: agendamentos.filter(a => 
      new Date(a.data_agendamento).toDateString() === new Date().toDateString()
    ).length
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 to-indigo-100">
      {/* Header */}
      <header className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center">
              <Link href="/">
                <h1 className="text-2xl font-bold text-gray-900 cursor-pointer">BioResonância</h1>
              </Link>
            </div>
            <nav className="hidden md:flex space-x-8">
              <Link href="/" className="text-gray-500 hover:text-gray-900">Início</Link>
              <Link href="/agendamento" className="text-gray-500 hover:text-gray-900">Agendar</Link>
              <Link href="/chatbot" className="text-gray-500 hover:text-gray-900">Chatbot</Link>
              <Link href="/admin" className="text-purple-600 font-medium">Admin</Link>
            </nav>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h2 className="text-3xl font-bold text-gray-900">Painel Administrativo</h2>
          <p className="mt-2 text-lg text-gray-600">
            Gerencie pontos de atendimento e visualize agendamentos
          </p>
        </div>

        {/* Estatísticas */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Total de Pontos</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-gray-900">{stats.totalPontos}</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Pontos Ativos</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{stats.pontosAtivos}</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Total Agendamentos</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-blue-600">{stats.totalAgendamentos}</div>
            </CardContent>
          </Card>
          
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Agendamentos Hoje</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-purple-600">{stats.agendamentosHoje}</div>
            </CardContent>
          </Card>
        </div>

        <Tabs defaultValue="pontos" className="space-y-6">
          <TabsList>
            <TabsTrigger value="pontos">Pontos de Atendimento</TabsTrigger>
            <TabsTrigger value="agendamentos">Agendamentos</TabsTrigger>
          </TabsList>

          <TabsContent value="pontos">
            <Card>
              <CardHeader>
                <div className="flex justify-between items-center">
                  <div>
                    <CardTitle>Pontos de Atendimento</CardTitle>
                    <CardDescription>Gerencie os locais de atendimento</CardDescription>
                  </div>
                  <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
                    <DialogTrigger asChild>
                      <Button 
                        className="bg-purple-600 hover:bg-purple-700"
                        onClick={() => {
                          setEditingPonto(null);
                          setFormData({
                            nome: "",
                            endereco: "",
                            cidade: "",
                            horarios: "",
                            capacidade_por_horario: 1,
                            status: "ativo"
                          });
                        }}
                      >
                        Novo Ponto
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-md">
                      <DialogHeader>
                        <DialogTitle>
                          {editingPonto ? "Editar Ponto" : "Novo Ponto de Atendimento"}
                        </DialogTitle>
                        <DialogDescription>
                          Preencha as informações do ponto de atendimento
                        </DialogDescription>
                      </DialogHeader>
                      <form onSubmit={handleSubmit} className="space-y-4">
                        <div>
                          <Label htmlFor="nome">Nome *</Label>
                          <Input
                            id="nome"
                            value={formData.nome}
                            onChange={(e) => handleInputChange("nome", e.target.value)}
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="endereco">Endereço *</Label>
                          <Input
                            id="endereco"
                            value={formData.endereco}
                            onChange={(e) => handleInputChange("endereco", e.target.value)}
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="cidade">Cidade *</Label>
                          <Input
                            id="cidade"
                            value={formData.cidade}
                            onChange={(e) => handleInputChange("cidade", e.target.value)}
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="horarios">Horários (separados por vírgula) *</Label>
                          <Input
                            id="horarios"
                            value={formData.horarios}
                            onChange={(e) => handleInputChange("horarios", e.target.value)}
                            placeholder="08:00, 09:00, 10:00, 14:00"
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="capacidade">Capacidade por Horário</Label>
                          <Input
                            id="capacidade"
                            type="number"
                            min="1"
                            value={formData.capacidade_por_horario}
                            onChange={(e) => handleInputChange("capacidade_por_horario", parseInt(e.target.value))}
                          />
                        </div>
                        <div>
                          <Label htmlFor="status">Status</Label>
                          <Select value={formData.status} onValueChange={(value) => handleInputChange("status", value)}>
                            <SelectTrigger>
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="ativo">Ativo</SelectItem>
                              <SelectItem value="inativo">Inativo</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>
                        <div className="flex justify-end space-x-2">
                          <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>
                            Cancelar
                          </Button>
                          <Button type="submit" className="bg-purple-600 hover:bg-purple-700">
                            {editingPonto ? "Atualizar" : "Criar"}
                          </Button>
                        </div>
                      </form>
                    </DialogContent>
                  </Dialog>
                </div>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Nome</TableHead>
                      <TableHead>Endereço</TableHead>
                      <TableHead>Cidade</TableHead>
                      <TableHead>Horários</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Ações</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {pontos.map((ponto) => (
                      <TableRow key={ponto.id}>
                        <TableCell className="font-medium">{ponto.nome}</TableCell>
                        <TableCell>{ponto.endereco}</TableCell>
                        <TableCell>{ponto.cidade}</TableCell>
                        <TableCell>{ponto.horarios.join(", ")}</TableCell>
                        <TableCell>{getStatusBadge(ponto.status)}</TableCell>
                        <TableCell>
                          <div className="flex space-x-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => handleEdit(ponto)}
                            >
                              Editar
                            </Button>
                            <Button
                              size="sm"
                              variant="destructive"
                              onClick={() => handleDelete(ponto.id)}
                            >
                              Excluir
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="agendamentos">
            <Card>
              <CardHeader>
                <CardTitle>Agendamentos</CardTitle>
                <CardDescription>Visualize todos os agendamentos realizados</CardDescription>
              </CardHeader>
              <CardContent>
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Cliente</TableHead>
                      <TableHead>Telefone</TableHead>
                      <TableHead>Ponto de Atendimento</TableHead>
                      <TableHead>Data</TableHead>
                      <TableHead>Horário</TableHead>
                      <TableHead>Status</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {agendamentos.map((agendamento) => (
                      <TableRow key={agendamento.id}>
                        <TableCell className="font-medium">{agendamento.nome}</TableCell>
                        <TableCell>{agendamento.telefone}</TableCell>
                        <TableCell>
                          <div>
                            <div className="font-medium">{agendamento.ponto_nome}</div>
                            <div className="text-sm text-gray-500">{agendamento.ponto_endereco}</div>
                          </div>
                        </TableCell>
                        <TableCell>
                          {new Date(agendamento.data_agendamento).toLocaleDateString("pt-BR")}
                        </TableCell>
                        <TableCell>{agendamento.horario}</TableCell>
                        <TableCell>{getStatusBadge(agendamento.status)}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
